 export default {
  //  props:['xuimg'],
   
  data() {
    return {
      swiperOptions: {
      //遇到得问题二、Vue 项目中 Swiper 轮播图无法显示 swiper-pagination 分页小圆点
      //这里显示小圆点  版本不同写法不同
      pagination:'.swiper-pagination',
      // 点击切换
      clickable:true,
        //反复滑动图片 loop
        //如果只有一个slide就锁住swiper
        //if(mySwiper.slides.length<=3){
        //mySwiper.lockSwipes();
        //}
      loop:true
      },
       "xuimg":[
              {
                  "id":"01",
                 imgtu: "https://imgs.qunarzz.com/vs_ceph_vcimg/b8c4527c41649814cc4cf86880abba54.jpeg"

              },
              {
                  "id":"02",
                  imgtu: "https://imgs.qunarzz.com/vs_ceph_vcimg/569cae4ae98e9793f7341e85eed73c54.jpeg"
              }
          ],
    }
  },
}



