import CiHeader from './page/Header'
import CiList from './page/list'
export default {
    name:'Header',
    components:{    
        CiHeader,
        CiList
    },
    data(){
        return{
            hotCities:[],
            cities:[]
        }
    },
    // 发送axios 再父组件传给子组件 然后 循环遍历到页面
     mounted(){
      this.$http.get("/api/city.json").then((res)=>{
        // console.log(res.data.data);
        const data= res.data.data;
        // 拿到热门地区
        this.hotCities=data.hotCities;
        // 拿到字母排序和地区
        this.cities=data.cities;
      })

    }
}



