//1、引入
import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home.vue'
import City from '@/components/Home/City/City.vue'
//2、注册
Vue.use(VueRouter)


//3、定义路由数组对象
const routes = [
  {
  path: '/',
  name:Home,
  component: Home
},
  {
    path: '/City',
    name: City,
    component: City
  },
]

//4、创建路由管理器
const router = new VueRouter({
  routes
})
//5、对外输出
export default router


